import React from 'react';
import {MainWrapper} from "../home/main-style";
import {AboutBlock} from "./AboutBlock";


const aboutData = [
    {
        id: 1,
        bg_img: "/images/about/background.png",
        bg_text: " ",
        justify: "flex-end",
        align: "right",
        big_text: " AAA     17 .    .      ,      ,          .         . "
    },
    {
        id: 2,
        bg_img: "/images/about/olumn.png",
        bg_text: "     ,             .         .",
        justify: "flex-start",
        align: "left",
        big_text: " BBB     17 .    .      ,      ,          .         . "
    },
    {
        id: 3,
        bg_img: "/images/about/interer.png",
        bg_text: "          .         .",
        justify: "flex-end",
        align: "right",
        big_text: " CCC     17 .    .      ,      ,          .         . "
    },
]


export const About = () => {
    return (
        <MainWrapper>
            {aboutData.map((el) => {
                return (
                    <AboutBlock key={el.id} id={el.id} bg_img={el.bg_img} bg_text={el.bg_text}
                                justify={el.justify} align={el.align} big_text={el.big_text}/>
                )
            })}
        </MainWrapper>
    );
};

